* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  color: #212121;
  line-height: 1.5;

  background: #2193b0;  
  background: linear-gradient(to right, #6dd5ed, #2193b0); 

}

.header {
  display: flex;
  align-items: flex-start;

  position: fixed;
  top: 0;
  display: block;
  height: 4rem;
  width: 100%;
  
  margin: 0 auto 1rem;
  padding-top: 1rem;

  background: linear-gradient(to bottom, rgb(40, 130, 150), rgba(13, 102, 125, 0.6)); 
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

}
.search-form {
  display: flex;
  align-items: center;
  margin: 0 auto;
  width: fit-content;

}
[name=searchQuery] {
  min-width: 250px;
  width: 50px;
  height: 2rem;

  padding-left: 12px; padding-right: 12px;

  font-size: 14px;

  border-radius: 5px 0 0 5px;
  border: none;
}

[name=searchQuery]:focus {
  box-shadow: inset 0px 1px 8px rgba(0, 0, 0, 0.4);
  outline: none;
}


button[type=submit]{
  width: 150px;
  height: 2rem;

  margin-right: 1rem;

  border-radius: 0 5px 5px 0;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;

  cursor: pointer;

    font-size: 14px;
  color: white;
  background: #2193b0;  
  background: linear-gradient(to right, #3ea2b8, #0d657b); 

  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.select-scroll-txt {
  margin-right: 0.7rem;
  font-size: 14px;
  color: white;
}

.select-scroll-gear:focus {
  box-shadow: inset 0px 1px 8px rgba(0, 0, 0, 0.4);
  outline: none;
}

.gallery {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 2rem;
  row-gap: 1rem;

  padding: 1rem;

  margin-top: 6rem;
  margin-bottom: 2rem;
}

.photo-card {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.info {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 12px;
  height: 3rem;

  background-color: white;

}
.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.load-more {
  display: block;
  margin: 0 auto 2rem;

  height: 2rem;
  width: 150px;

  border-radius: 5px;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;

  cursor: pointer;

  color: white;
  background: #2193b0;  
  background: linear-gradient(to right, #3ea2b8, #0d657b); 
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;

}

.is-hidden {
  display: none;
}
.is-vivible {
  display: block;
}

